.loader {
    text-align: center;
}.loader span {
    display: inline-block;
    width: 80px;
    height: 80px;
	margin: -280px 40px 54px  -34px;

    background: #ff0000;
	background:url("./Images/Flower3.png");
    background-size: cover;
    background-position: center;
    background-size: 40px;
    background-repeat: no-repeat;
    border-radius: 50%;
    animation: loader 5s infinite  linear;
}

.loader span:nth-child(5n+5) {
    animation-delay: 1.3s;
}

.loader span:nth-child(3n+2) {
    animation-delay: 1.5s;
}

.loader span:nth-child(2n+5) {
    animation-delay: 1.7s;
}

.loader span:nth-child(3n+10) {
    animation-delay: 2.7s;
}

.loader span:nth-child(7n+2) {
    animation-delay: 3.5s;
}

.loader span:nth-child(4n+5) {
    animation-delay: 5.5s;
}

.loader span:nth-child(3n+7) {
    animation-delay: 8s;
}
@-webkit-keyframes loader {
  0% {
    width: 80px;
    height: 80px;
    opacity: 1;

	-webkit-transform: translate(0, 0px) rotateZ(0deg);
  }
  75% {
    width: 80px;
    height: 80px;
    opacity: 1;

	-webkit-transform: translate(100px, 600px) rotateZ(270deg); 
  }
  100% {
    width: 80px;
    height: 80px;
    opacity: 0;

	-webkit-transform: translate(150px, 800px) rotateZ(360deg);
  }
}
@keyframes loader {
  0% {
    width: 80px;
    height: 80px;
    opacity: 1;
    transform: translate(0, 0px) rotateZ(0deg);
  }
  75% {
    width: 80px;
    height: 80px;
    opacity: 1;
    transform: translate(100px, 600px) rotateZ(270deg);
  }
  100% {
    width: 80px;
    height: 80px;
    opacity: 0;
    transform: translate(150px, 800px) rotateZ(360deg);
  }
}
